<template>
	<view class="content" v-show="pages_if">
		<view class="king_backimg">
			<image class="imga" :src="king_user.avatar" mode="aspectFill"></image>
			<view class="king_position">
				<image class="imgb" :src="king_user.avatar" mode="aspectFill"></image>
				<view class="position_name">
					<view>{{king_user.title}}</view>
					<view>{{king_user.telephone}}</view>
				</view>
			</view>
		</view>
		<!-- 内容部分 -->
		<view class="inp_con">
			<!-- 实时金价 -->
			<view class="king_pic">
				<view class="king_pic_a">
					<zs-title :titleRed="'实时'" :title="'金价'" :page_show="true"></zs-title>
					<view class="times">
						<u-icon name="clock-fill" size="26" color="#3b4e85" style="margin-right: 10rpx;margin-top: 16rpx;"></u-icon>
						{{gold_price.time}}
					</view>
				</view>
				<!-- 表格 -->
				<view class="tabs">
					<view class="tabs_tr">
						<view>品种</view>
						<view>最新价</view>
						<view>买入价</view>
						<view>卖出价</view>
						<view>涨跌</view>
					</view>
					<view class="swipers_d">
						<view :class="ind % 2 != 0 ? 'tabs_tr' :'tabs_td'" v-for="(it,ind) in gold_price.data" :key="ind">
							<view>{{it.title}}</view>
							<view>{{it.new_price}}</view>
							<view>{{it.buy_price}}</view>
							<view>{{it.sell_price}}</view>
							<view> <text v-if="it.proportion_type == 0" style="color: #5cb671;">↓ {{it.proportion}}</text>
								<text style="color: #f5553f;" v-else>↑ {{it.proportion}}</text> </view>
						</view>
					</view>
				</view>
			</view>
			<!-- 九宫格 -->
			<view class="nine_g">
				<view class="nine_g_child" v-for="(it,ind) in index_data.label" :key="ind" v-if="ind <= 7" @click="go_textrue(it.id)">
					<view class="nine_g_child_tit">
						{{it.title}}
					</view>
					<view class="nine_g_child_cla">
						{{it.remark}}
					</view>
					<image :src="it.image" mode="aspectFill"></image>
				</view>
			</view>
			<!-- 筛选 -->
			<scroll-view class="scroll-view_H" scroll-x="true">
				<view id="demo1" class="nav_swiper" :class="{active: it.id == nav_ind }" @click="nac_cla(it.id)" v-for="(it,ind) in index_data.cates"
				 :key="ind">
					{{it.title}}
				</view>
			</scroll-view>
		</view>
		<view class="classify">
			<zs-shoplist-type :shop_list="shop_list" :lists="list" :cate_fist_id="nav_ind" :shop_subject_id="''" @shop_confim="shop_confim"></zs-shoplist-type>
		</view>
	</view>
</template>

<script>
	// import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
	import dragButton from "@/components/drag-button/drag-button.vue";
	export default {
		data() {
			return {
				puytcopup: 0, //普通优惠券
				tops: 0,
				swiperCurrent: 0,
				swiperCurrent_b: 0,
				backgroundColor: '', //标题栏背景色
				headcolor: '#fff', //消息颜色
				indexbackcolor: 'rgba(255,2555,255,0.28)', //导航栏搜索框背景色
				end_time: '', //秒杀到期
				end_seckill: '00:00:00:00', //倒计时
				nav_ind: 0, //导航
				show: true,
				value1: 1,
				index_data: '', //首页数据
				shop_list: '',
				list: '',
				huiy_show: false, //会员状态
				gold_price: [],
				// coupon_data:'',//优惠券
				ptcoupon: false, //普通优惠券状态
				xrcoupon: false, //新人优惠券状态
				second: '',
				king_user: '',
				pages_if: true,
				code:'',
				member_id:'',
				stat:''
			}
		},
		onPageScroll(e) {
			// console.log(e)
			// this.backgroundColor = 'rgba(255,255,255,'+e.scrollTop / 180 +')' 
			// this.headcolor = 'rgba(0,0,0,'+e.scrollTop / 180 +')'
			// this.indexbackcolor = 'rgba(241,241,241,'+e.scrollTop / 180 +')'//导航栏搜索框背景色
			// if(e.scrollTop < 40){
			// 	this.headcolor = '#fff'
			// 	this.indexbackcolor ='rgba(255,2555,255,0.28)'//导航栏搜索框背景色
			// }
		},
		// components: {uniNavBar},
		onUnload() {
			uni.setStorageSync('coupon', 0) 
		},
		onLoad(op) {
			//用户id
			// console.log(op)
			// var reg = new RegExp("(^|&)" + 'token' + "=([^&]*)(&|$)");
			// let r = window.location.search.substr(1).match(reg);	
			// console.log(r)
			// let tok =''
			// if(r !== null){
			// 	tok = unescape(r[2])	
			// 	uni.setStorageSync('token',tok)
			// }else{
			// 	this.wxAuthorize(op.data)
			// }
			// this.page_render()
			uni.setStorageSync('token','eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiIhQCMkJSomIiwiYXVkIjoiIiwiaWF0IjoxNjE4OTg1NDA1LCJuYmYiOjE2MTg5ODU0MDgsImV4cCI6MTYyMDk4NTQwNSwiZGF0YSI6eyJ1aWQiOjN9fQ.okI6OL8fR1ajOPzk7Qpl13iBojn01QWAYWJmrZh9UT0')
		},
		methods: {
			//code
			GetQueryString(code) {
				var reg = new RegExp("(^|&)" + code + "=([^&]*)(&|$)");
				  // var r = url.split('?')[1].match(reg);
				let r = window.location.search.substr(1).match(reg);
				if (r != null) return unescape(r[2]);
				
				return null;
			},
			//参数
			stateNum(state) {
				var reg = new RegExp("(^|&)" + state + "=([^&]*)(&|$)");
				  // var r = url.split('?')[1].match(reg);
				let r = window.location.search.substr(1).match(reg);
				if (r != null) return unescape(r[2]);
				
				return null;
			},
			wxAuthorize(a) {  
			   let link =  window.location.href;  
			   let code =  this.GetQueryString('code') // code
			   
			 
			   console.log(code)
			    // 如果拿到code，调用授权接口，没有拿到就跳转微信授权链接获取   
			    if (code == null || code == "") {  
					let appid = 'wxa41c78ae3465d0fa';
								
					let uri = encodeURIComponent(link);
					
					window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${uri}&response_type=code&scope=snsapi_base&state=${a}#wechat_redirect`;  
					
			    }else {
					let state_s = this.stateNum('state') // state
					console.log(state_s)
					this.stat = JSON.parse(state_s)
					//用户id
					if(this.stat.name){
						uni.setStorageSync('member_id',this.stat.name)
					}
			       	this.$api.get('wechat_login',{code:code}).then(res=>{
			       		console.log(res)
						if(res.status== 1){
							uni.setStorageSync('token',res.data.token)
							if(this.stat.type == 1){
								uni.navigateTo({
									url:'./shop_detail?shop_id=' + this.stat.id
								})
							}
						}
			       	})	
			    }  
			},
			page_render() {
				this.$api.get('index').then(res => {
					// console.log(res.data)
					if (res.status == 1) {
						this.index_data = res.data
						this.end_time = res.data.kill.title_end
						this.nav_ind = res.data.cates[0].id

						uni.setStorageSync('jinx', this.index_data.zhuanti) //精选专题

						this.nac_cla(res.data.cates[0].id)
						uni.stopPullDownRefresh()

					}
				})
				//实时金价
				this.$api.get('gold_price', {id: uni.getStorageSync('member_id')}).then(res => {
					// console.log(res)
					if (res.status == 1) {
						this.gold_price = res
					}
				})
				this.$api.get('manageh5', {member_id: uni.getStorageSync('member_id')}).then(res => {
					console.log(res)
					if (res.status == 1) {
						this.king_user = res.data
					}
				})
			},
			//弹框隐藏
			nopop() {
				this.ptcoupon = false //普通优惠券状态
				this.xrcoupon = false //新人优惠券状态
			},
			// 优惠券
			coupon(e) {
				// this.$api.post('coupon',{type:e}).then(res=>{
				// 	console.log(res)
				// 	if(res.status == 1){
				// 		this.coupon_data = res.data
				// 		if(this.puytcopup == 0 && res.data.data != ''){
				// 			this.ptcoupon = true
				// 		}
				// 		if(this.puytcopup == 1 && res.data.data != ''){
				// 			this.xrcoupon = true
				// 		}
				// 	}
				// })
			},
			//点击材质
			go_textrue(e) {
				this.com.navto('./nine_nav?id=' + e + '&data=' + JSON.stringify(this.index_data.label))
			},
			//团购进详情
			go_teamshop(e) {
				this.com.navto('../../pages/index/Activityshop_detail?shop_id=' + e + '&type=' + 1)
			},
			// 确定筛选
			shop_confim(e) {
				this.$api.post('goods', e).then(res => {
					console.log(res)
					if (res.status == 1) {
						this.shop_list = res.data.data
					}
				})
			},
			//个人中心
			// page_my(){
			// 	this.com.rel('../my/my')
			// },
			//页面跳转
			go_pages(e) {
				this.com.navto(e)
			},

			//专题点击
			special_cli(id, type) {
				//定制
				if (type == 4) {
					this.com.navto('./customization')
				} else {
					this.com.navto('./Selected_topics?id=' + id + '&type=' + JSON.stringify(this.index_data.zhuanti))
				}

			},

			//轮播指示点
			swiperChange(e) {
				// 	const index = e.detail.current;
				// 	this.swiperCurrent = index;
			},
			// swiperChange_b(e) {
			// 	const index = e.detail.current;
			// 	this.swiperCurrent_b = index;
			// },
			//导航栏点击
			nac_cla(e) {
				this.nav_ind = e
				//商品
				this.$api.post('goods', {
					cate_fist_id: this.nav_ind
				}).then(res => {
					console.log(res)
					if (res.status == 1) {
						this.shop_list = res.data.data
					}
				})
				//筛选条件
				this.$api.get('screen', {
					cate_id: this.nav_ind
				}).then(res => {
					// console.log(res)
					if (res.status == 1) {
						this.list = res.data
					}
				})
			},
		},

	}
</script>

<style lang="scss" scoped>
	.king_backimg {
		width: 750upx;
		height: 360upx;
		// background-size: 100% 100%;
		position: relative;

		.imga {
			width: 100%;
			height: 100%;
		}

		.king_position {
			position: absolute;
			left: 30upx;
			bottom: 60upx;
			display: flex;

			image {
				width: 100rpx;
				height: 100rpx;
				border-radius: 50%;
				border: 2rpx solid white;
			}

			.position_name {
				color: white;
				padding-left: 10rpx;

				view:nth-child(1) {
					margin-top: 10rpx;
					font-size: 30rpx;
				}
			}
		}
	}

	.scroll-view_H {
		display: flex;
		white-space: nowrap;
		line-height: 90rpx;
		height: 90rpx;
		border-bottom: 1rpx solid #eee;

		.nav_swiper {
			width: 20%;
			text-align: center;
			display: inline-block;
			position: relative;

			&.active {
				color: #2d407a;

				&:before {
					content: '';
					position: absolute;
					left: 35%;
					bottom: 10%;
					transform: translateY(-50%);
					height: 7rpx;
					width: 30%;
					background-color: #2d407a;
					border-radius: 0 4px 4px 0;
					opacity: .8;
				}
			}
		}
	}

	.classify {
		width: 100%;
		margin-bottom: 50rpx;
		background-color: #F6F6F6;
	}

	@import './index.scss'
</style>
